import { ArrowForward } from "@mui/icons-material";
import { Button, Box, Container, Grid, Typography, useTheme } from "@mui/material";
import { Link } from "react-router-dom";

const MiniAbout = () => {
	const theme = useTheme();

	return (
		<Container sx={{ textAlign: "center" }}>
			<Grid container spacing={2} justifyContent="center" alignItems="center" sx={{ my: 20 }}>
				<Grid item xs={12} md={5}>
					<img
						src={`/${process.env.REACT_APP_BASENAME}/static/experiment.png`}
						alt="cars.zim"
						style={{ width: "100%" }}
					/>
				</Grid>

				<Grid item xs="auto">
					<Typography
						variant="h4"
						sx={{
							mb: 2,
							color: "inherit",
							maxWidth: "500px",
							fontFamily: "kalam, cursive,roboto, serif",
						}}
					>
						A
						<Typography
							variant="inherit"
							component="span"
							sx={{ color: "success.main", mx: 0.7 }}
						>
							Nation-wide
						</Typography>
						Billboard
					</Typography>

					<Typography
						variant="body1"
						sx={{
							color: "inherit",
							maxWidth: "500px",
						}}
					>
						Cars.zim provides a way for people looking to sell their vehicles reach more
						people nation-wide. The best part is its all for free! Simply create an
						account and fill out a few details to get your vehicle advertised on the
						site. All this within minutes
					</Typography>

					<Button
						component={Link}
						endIcon={<ArrowForward />}
						to="/signup"
						variant="outlined"
						color="success"
						sx={{ mt: 2, borderRadius: "3rem", textTransform: "capitalize" }}
					>
						Get Started
					</Button>
				</Grid>
			</Grid>

			<Grid container spacing={2} justifyContent="center" alignItems="center" sx={{ my: 20 }}>
				<Grid item xs="auto">
					<Typography
						variant="h4"
						sx={{
							mb: 2,
							color: "inherit",
							maxWidth: "500px",
							fontFamily: "kalam, cursive,roboto, serif",
						}}
					>
						All
						<Typography
							variant="inherit"
							component="span"
							sx={{ color: "success.main", mx: 0.7 }}
						>
							Shapes &amp; Sizes
						</Typography>
					</Typography>

					<Typography
						variant="body1"
						sx={{
							color: "inherit",
							maxWidth: "500px",
						}}
					>
						Browse countless listings posted by people across the country, covering
						several categories, manufacturers etc. Our platform makes it simple for you
						to find the right vehicle for your needs.
					</Typography>

					<Button
						component={Link}
						endIcon={<ArrowForward />}
						to="/offers"
						variant="outlined"
						color="success"
						sx={{ mt: 2, borderRadius: "3rem", textTransform: "capitalize" }}
					>
						Find a car
					</Button>
				</Grid>

				<Grid item xs={12} md={5}>
					<img
						src={`/${process.env.REACT_APP_BASENAME}/static/experiment.png`}
						alt="cars.zim"
						style={{ width: "100%" }}
					/>
				</Grid>
			</Grid>
		</Container>
	);
};

export default MiniAbout;
